.avatar {
  border-radius: 50%;
  background-size: cover;
  border: 2px solid var(--color-commonWhite);
}

.avatarContainer {
  display: flex;
  justify-content: center;
  position: relative;
  z-index: 0;

  @media (--mq-is-mobile) {
    justify-content: flex-start;
  }
}

.avatar:nth-child(1) {
  margin-left: 0;
}

.largeAvatar {
  width: 84px;
  height: 84px;
  margin-left: -24px;
}

.mediumAvatar {
  width: 68px;
  height: 68px;
  margin-left: -24px;
}

.smallAvatar {
  width: 38px;
  height: 38px;
  margin-left: -12px;
}
