.dropSection {
  scroll-margin-top: var(--dlp-overviewHeight);
}

.dropSectionTitle {
  padding: 40px 24px 0 24px;
  text-align: center;

  @media (--mq-is-mobile) {
    padding: 32px 24px 0 24px;
    text-align: start;
  }
}

.dropSubSectionTitle {
  margin: 32px 24px 40px 24px;

  @media (--mq-is-mobile) {
    margin: 24px 24px 32px 24px;
  }

  &.additional {
    margin-bottom: 48px;
    margin-top: 48px;

    @media (--mq-is-mobile) {
      margin-bottom: 40px;
      margin-top: 40px;
    }
  }
}

.dropSubSectionItems {
  display: flex;
  flex-direction: column;
  gap: 24px;
  width: 100%;
}

.dropCardContainer {
  height: 760px;

  @media (--mq-is-desktop) {
    height: 700px;
  }

  @media (--mq-is-mobile) {
    height: initial;
  }
}
