.heroSection {
  color: var(--color-commonWhite);
  min-height: 720px;
  position: relative;
  text-align: center;

  &::before {
    background-image: var(--dlp-heroBackgroundImage);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    content: " ";
    display: block;
    height: calc(105% - var(--dlp-heroGradientHeight));
    position: absolute;
    width: 100%;
    z-index: 0;
  }
}

.gradient {
  background-image: var(--dlp-heroGradient);
  left:0;
  padding: 250px calc(min((100vw - 1000px) / 2, 149px)) 60px;
  position: relative;
  top: 0;
  z-index: 1;
}

.title {
  margin: 0 0 50px;
}

.heroVideo {
  position: relative;
  object-fit: cover;
  width: 100%;
  max-width: 1146px;
  max-height: 573px;
  height: calc(50vw - 149px);
  border-radius: 48px;
}

.centerContent {
  max-width: 1142px;
  margin: auto;
}

.description {
  white-space: pre-wrap;
  word-wrap: break-word;
  max-width: min(800px, 70%);
  margin: 50px auto 32px;
}

.moreInfoBtn {
  --actionButtonColor-hover: rgba(255,255,255,0.6);
  --actionButtonColor-active: rgba(255,255,255,0.2);
  --actionButtonColor: var(--color-commonWhite);
}

.moreInfoBtnTxt {
  white-space: nowrap;
}

.videoSection {
  width: 100%;
  height: 0;
  overflow: visible;
  padding-top: 53.6%;
  position: relative;
}

.videoSectionAbs {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
}

@media (--mq-is-mobile) {
  .heroSection {
    text-align: start;
  }

  .heroVideo {
    height: calc(50vw - 24px);
  }

  .description {
    width: 100%;
    max-width: unset;
    margin: 50px 0 32px;
  }

  .gradient {
    --horz-padding: 24px;
    padding: 240px var(--horz-padding) 60px;
  }
}
