.container {
  background-color: var(--color-backgroundDefault);
  border-bottom: 1px solid var(--color-SolidNeutralGray1);
  border-top: 1px solid var(--color-SolidNeutralGray1);
  box-sizing: border-box;
  height: var(--dlp-overviewHeight);
  overflow: hidden;
  position: sticky;
  top: 0;
  z-index: 1;

  @media (--mq-is-mobile) {
    overflow-x: auto;
  }
}

.container > ul {
  display: flex;
  flex-direction: row;
  gap: 24px;
  justify-content: center;
  margin: 0 auto;
  overflow: hidden;
  padding: 13px 24px;
  width: max-content;

  @media (--mq-is-mobile) {
    margin: 0;
  }
}

.container > ul > li {
  align-items: center;
  display: flex;
  flex-direction: row;
  gap: 5px;
  list-style: none;
}

.container > ul > li > .count {
  align-items: center;
  background-color: var(--color-SolidNeutralGray5);
  border-radius: 32px;
  box-sizing: border-box;
  color: var(--color-backgroundDefault);
  display: flex;
  height: 21px;
  justify-content: center;
  min-width: 21px;
  padding: 0px 8px;

  &.singleDigit {
    overflow: hidden;
    width: 21px;
  }
}
