.activityTitleContainer {
  padding: 80px 0 60px 0;

  @media (--mq-is-mobile) {
    justify-content: flex-start;
    padding: 80px 24px 60px 24px;
  }
}

.activityTableContainer {
  margin: 0 0 80px 0;

  @media (--mq-is-mobile) {
    margin: 0 24px 60px 24px;
  }
}
.activityTableContainer .activityTable {
  margin: 28px 0 0 0;

  @media (--mq-is-mobile) {
    margin: 16px 0 0 0;
  }

  & .activityCard {
    background-color: var(--color-backgroundDefault);
    border-color: var(--color-backgroundDefault);
  }

  @media (--mq-is-not-mobile) {
    &:hover .activityCard {
      background-color: var(--color-commonWhite);
      border-color: var(--color-SolidNeutralGray1);

      &:hover {
        background-color: var(--color-backgroundDefault);
        border-color:var(--color-backgroundDefault);
      }
    }
  }
}

.title {
  margin-bottom: 25px;
  display: flex;
  justify-content: center;

  @media (--mq-is-mobile) {
    justify-content: flex-start;
  }
}
