.dialogContainer {
  padding: 32px 24px;
}

.dialogContainer > hr:last-child {
  display: none;
}

.description {
  white-space: pre-wrap;
  word-wrap: break-word;
}

.moreInfoVideo {
  position: relative;
  object-fit: cover;
  width: 458px;
  height: 229px;
  border-radius: 16px;
  align-items: center;
}

@media (--mq-is-mobile) {
  .moreInfoVideo {
    width: calc(100vw - 48px);
    height: calc(50vw - 24px);
  }
}
