.artists {
  margin: 50px auto;
}

.artists:not(.multiRow) {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 24px;
}

.artists.multiRow {
  display: grid;
  grid-template-columns: repeat(5, minmax(0, 1fr));
  max-width: 1000px;
  grid-row-gap: 32px;
  grid-column-gap: 24px;
}

.artist {
  align-items: center;
  display: flex;
  flex-wrap: nowrap;
  max-width: 176px;
  overflow: hidden;
  width: auto;
}

.artist .transparentStats {
  opacity: 0.55;
}

.artists.single > .artist {
  max-width: 300px;
}

.artists.multiRow > .artist {
  width: 100%;
}

@keyframes marqueeAnimation {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(calc(-50% - var(--gap, 0) / 2));
  }
}

.marqueeContainer {
  width: 100%;
  overflow: hidden;
  position: relative;
  margin-bottom: 24px;
}

.marqueeContainer.isAnimating {
  width: calc(100% + 2 * var(--horz-padding, 24px));
  left: calc(-1 * var(--horz-padding, 24px));
}

.marquee {
  --gap: 24px;
  display: flex;
  flex-wrap: nowrap;
  width: max-content;
  animation: marqueeAnimation linear infinite;
  animation-duration: 10s;
  gap: var(--gap);
}

.marquee:hover {
  animation-play-state: paused;
}

  /* stylelint-disable-next-line no-descending-specificity */
.marquee > .artist {
  flex: 1 0 auto;
  width: auto;
}

@media (--mq-is-mobile) {
  .artists.single:not(.multiRow) {
    justify-content: flex-start;
  }
}
