.dropPage {
  --dlp-overviewHeight: 49px;
}

.dropPage :global(.defaultLink) {
  color: var(--color-commonBlack);

  &:hover {
    color: var(--color-SolidNeutralGray3);
    text-decoration: none;
  }

  &:active {
    color: var(--color-SolidNeutralGray1);
  }
}

.dropPage > section > .sectionContent {
  box-sizing: border-box;
  margin: 0 auto;
  max-width: 1695px;
  padding: 0 0 80px;
  width: 100%;

  @media (--mq-is-desktop) {
    padding: 0 32px 40px;
    max-width: 1440px;
  }

  @media (--mq-is-mobile) {
    padding: 0 0 24px;
  }
}

.loader {
  min-height: 50vh;
}
